<template>
  <el-row>
    <el-col>
      <div>
          <div>{{CountValue}}</div>     // storecount
          <button @click="increment">+</button>
          <button @click="decrement">-</button>
      </div>
    </el-col>
  </el-row>
</template>
<script>
    import { mapActions, mapGetters } from 'vuex';     // mapActions, mapGetters
    export default {
        data() {
          return {
          }
        },
        methods: {
          ...mapActions([
                    'increment',    // this.increment()  this.$store.dispatch('increment')
           'decrement'
         ]),
        },
        computed: {
            ...mapGetters({      // store  getters 
                    CountValue:'getCount'    // getCount getters.js
            })
        }
    }
</script>
<style>
</style>



