
<template>
	<el-row>
		<!---->
		<el-col :span="64" class="toolbar" style="padding-bottom: 0px;with:100%;height:100%">
			<el-form :inline="true" :model="filters" ref="filters">
				<el-form-item>
					<el-input placeholder="" v-model="filters.keyword1"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" class="el-icon-search" v-on:click="getFormData1"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="success" class="el-icon-plus" v-on:click="showDialogForm"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="danger" class="el-icon-delete" @click="batchDelete"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<el-col>
			<el-table
			v-loading="listLoading" element-loading-text=""
      :data="tableData1"
			@selection-change="handleSelectionChange1">
			<el-table-column type="selection" width="55">
			</el-table-column>
<!--      <el-table-column
        prop="id"
        label="id">
      </el-table-column> -->
	  <el-table-column
	    prop="examCourse"
	    label="" sortable>
	  </el-table-column>
	  <el-table-column
	    prop="major"
	    label="" sortable>
	  </el-table-column>
	  <el-table-column
	    prop="institute"
	    label="" sortable>
	  </el-table-column>
	  <el-table-column
	    prop="examDate"
	    label="" sortable>
	  </el-table-column>
	  <el-table-column
	    prop="totalTime"
	    label="()" sortable>
	  </el-table-column>
	  <el-table-column
	    prop="totalScore"
	    label="" sortable>
	  </el-table-column>
	  <!--  -->
		<el-table-column
			  fixed="right"
			  label=""
					width="180px"
					align="center">
			  <template slot-scope="scope">
				<el-button v-if="scope.row.totalScore!=0"
					  @click.native="viewPaper(scope.row.id)"
					  type="text"
					  size="small">
				  
				</el-button>
				<el-button v-else
					@click.native="randomPaper(scope.row.paperId)"
					type="text"
					size="small">
					
				</el-button>
			  </template>
		</el-table-column>	  
    </el-table>
		</el-col>

		<el-col>
			<div class="block" style="float: right;margin-right: 10px;margin-top: 10px;">
				<el-pagination
					@size-change="handleSizeChange1"
					@current-change="handleCurrentChange1"
					:current-page="startPage1"
					:page-sizes="pageSizes1"
					:page-size="pageSize1"
					layout="total, sizes, prev, pager, next, jumper"
					:total="total1">
				</el-pagination>
		 </div>
	</el-col>

<!--  -->
	<el-dialog title="" :visible.sync="dialogFormVisible1">
	<div style="width:60%;margin: 0 auto">
	<el-form ref="attr" :model="attr" :inline="false" label-width="90px" class="demo-ruleForm">
		
	<el-form-item label="" prop="instituteId2">
		<el-select v-model="attr.instituteId2" filterable placeholder="" @change="getMajorData">
			<el-option
				v-for="item in institutes"
				:key="item.id"
				:label="item.name"
				:value="item.id">
			</el-option>
	   </el-select>
	</el-form-item>	
	<el-form-item label="" prop="majorId2">
		<el-select v-model="attr.majorId2" filterable placeholder="">
			<el-option
				v-for="item in majors"
				:key="item.id"
				:label="item.name"
				:value="item.id">
			</el-option>
	   </el-select>
	</el-form-item>	
	<el-form-item label="" prop="name2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
		<el-input  type="text" v-model="attr.name2" placeholder="" auto-complete="off"></el-input>
	</el-form-item>
	<el-form-item label="" prop="desc2">
		<el-input  type="text" v-model="attr.desc2" placeholder="" auto-complete="off"></el-input>
	</el-form-item>
	<el-form-item label="" prop="date2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
		<el-input  type="date" v-model="attr.date2" placeholder="" auto-complete="off"></el-input>
	</el-form-item>
	<el-form-item label="()" prop="time2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-9]\d*$/, message: '', trigger: 'change,blur'}]">
		<el-input  type="number" v-model="attr.time2" placeholder="" auto-complete="off"></el-input>
	</el-form-item>
	</el-form>
 </div>
	<div slot="footer" class="dialog-footer">
		<el-button @click="dialogFormVisible1 = false"> </el-button>
		<el-button @click="resetForm('attr')"></el-button>
		<el-button type="primary" @click="submitForm('attr')"> </el-button>
	</div>
</el-dialog>

<!--  -->
	<el-dialog title="" :visible.sync="dialogFormVisible2">
	<div style="width:60%;margin: 0 auto">
	<el-form ref="attr" :model="attr" :inline="false" label-width="90px" class="demo-ruleForm">
		<el-form-item label="" prop="fillCnt2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-9]\d*$/, message: '', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.fillCnt2" placeholder="" auto-complete="off"></el-input>
		</el-form-item>
<!-- 		<el-form-item label="" prop="fillScore2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-5]$/, message: '1-5', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.fillScore2" placeholder="" auto-complete="off"></el-input>
		</el-form-item> -->
		<el-form-item label="" prop="judgeCnt2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-9]\d*$/, message: '', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.judgeCnt2" placeholder="" auto-complete="off"></el-input>
		</el-form-item>
<!-- 		<el-form-item label="" prop="judgeScore2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-5]$/, message: '1-5', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.judgeScore2" placeholder="" auto-complete="off"></el-input>
		</el-form-item> -->
		<el-form-item label="" prop="singleCnt2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-9]\d*$/, message: '', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.singleCnt2" placeholder="" auto-complete="off"></el-input>
		</el-form-item>
<!-- 		<el-form-item label="" prop="singleScore2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-5]$/, message: '1-5', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.singleScore2" placeholder="" auto-complete="off"></el-input>
		</el-form-item> -->
<!-- 		<el-form-item label="" prop="multipleCnt2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-9]\d*$/, message: '', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.multipleCnt2" placeholder="" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item label="" prop="multipleScore2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^[1-5]$/, message: '1-5', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.multipleScore2" placeholder="" auto-complete="off"></el-input>
		</el-form-item> -->
	</el-form>
 </div>
	<div slot="footer" class="dialog-footer">
		<el-button @click="dialogFormVisible2 = false"> </el-button>
		<el-button @click="resetForm('attr')"></el-button>
		<el-button type="primary" @click="startPaper()"></el-button>
	</div>
</el-dialog>

	</el-row>


</template>

<script>
import http from '../../utils/http'
export default {
	data() {
		return {
			filters: {
				keyword1: ''
			},
			attr: {
				name2: '',
				majorId2: '',
				instituteId2: '',
				desc2: '',
				date2: '',
				time2: '',
				fillCnt2: '',
				judgeCnt2: '',
				singleCnt2: '',
				multipleCnt2: 0,
				fillScore2: '',
				judgeScore2: '',
				singleScore2: '',
				multipleScore2: 0,
				paperId2: ''
			},
			institutes:[],    //
			majors:[],       //
			listLoading: false, // 
			pageSizes1: [30, 50, 80, 100],
			pageSizes2: [5, 10],
			startPage1: 1,
			startPage2: 1,
			pageSize1: 30,
			pageSize2: 5,
			total1: 0,
			total2: 0,
			tableData1: [],
			tableData2: [],
			total1: 0,
			total2: 0,
			page1: 1,
			page2: 1,
			dialogTableVisible1: false,
			dialogFormVisible1: false,
			dialogFormVisible2: false,
			innerVisible: false,
			attrIds: [], // ids
			detailIds: [], // ids
			attrId: ''
		}
	},
	methods: {
		// 
		async getFormData1 () {
			let _this = this
			_this.listLoading = true
			let params = {
				startPage: _this.startPage1,
				pageSize: _this.pageSize1,
				name: _this.filters.keyword1
			}
			let data = await http.get('exam/list', params)

			if(!data.data) {
				_this.listLoading = false
				return
			}

			if (data.data.status === 200) {
				 _this.tableData1 = data.data.data
				 _this.total1 = data.data.total
			} else {
				_this.message(true,data.data.msg,'error')
				_this.formData1 = []
			}
			_this.listLoading = false
		},
		// 
		showDialogForm() {
			this.dialogFormVisible1 = true
			this.getInstituteData()
		},
		// 
		async getInstituteData () {
				let _this = this
				let data = await http.get('institute/findAllInstitute')
				if(!data.data) {
					return
				}
				if (data.data.status === 200) {
				   _this.institutes = data.data.data
				 } else {
				  _this.message(true,data.data.msg,'error')
				  _this.institutes = []
				}
			},
			
		//
		async getMajorData (){
			let _this = this
			let param = {
				instituteId: _this.attr.instituteId2
			}
			let data = await http.get('major/findAllMajor',param);
			if(!data.data) {
				return
			}
			if (data.data.status === 200) {
			   _this.majors = data.data.data
			 } else {
			  _this.message(true,data.data.msg,'error')
			  _this.majors = []
			}			
		},
		//
		viewPaper(id){
			alert("")
		},
		//
		randomPaper(paperId){
			this.dialogFormVisible2 = true
			this.attr.paperId2=paperId
		},
		//
		async startPaper(){
			let _this = this
			let params = {
				paperId: _this.attr.paperId2,
				fillCnt: _this.attr.fillCnt2,
				fillScore: _this.attr.fillScore2,
				judgeCnt: _this.attr.judgeCnt2,
				judgeScore: _this.attr.judgeScore2,
				singleCnt: _this.attr.singleCnt2,
				singleScore: _this.attr.singleScore2,
				multipleCnt: _this.attr.multipleCnt2,
				multipleScore: _this.attr.multipleScore2
			}
			let data = await http.post("paper/add", params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				  _this.resetForm('attr')
				  _this.dialogFormVisible2=false
          _this.message(true,data.data.msg,'success')
					_this.getFormData1()
			} else {
         _this.message(true,data.data.msg,'error')
			}			
		},
		// 
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addInstitute()
				} else {
					console.log('error submit!!');
					return false
				}
			})
		},
		// 
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		// 
		async addInstitute() {
			let _this = this
			let params = {
				examCourse: _this.attr.name2,
				examDesc: _this.attr.desc2,
				examDate: _this.attr.date2,
				totalTime: _this.attr.time2,
				major: _this.attr.majorId2,
				institute: _this.attr.instituteId2
			}
			let data = await http.post("exam/add", params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				  _this.resetForm('attr')
				  _this.dialogFormVisible1=false
          _this.message(true,data.data.msg,'success')
					_this.getFormData1()
			} else {
         _this.message(true,data.data.msg,'error')
			}
		},
		// 
		batchDelete () {
			if (this.attrIds.length === 0) {
				 this.message(true,'','error')
				 return
			}
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'error'
			}).then(() => {
                this.doDelete()
			}).catch(() => {
				this.message(true,'','error')
			})
		},
		// 
		async doDelete () {
			let params = {
				ids: this.attrIds
			}
			let data =await http.post('exam/delete', params)
			if(!data.data) {
				return
			}
			if (data.data.status === 200) {
				 this.message(true,data.data.msg,'success')
			} else {
				 this.message(true,data.data.msg,'error')
			}
			this.getFormData1()
		},
		// 
		handleSelectionChange1(val) {
			this.attrIds = []
			if (val) {
			 val.forEach(row => {
				 this.attrIds.push(row.id)
			 });
		 }
		},
		// 
		handleSizeChange1 (val) {
			this.pageSize1 = val
			this.getFormData1()
		},
		// 
		handleCurrentChange1 (val) {
			this.startPage1 = val
			this.getFormData1()
		},
		/**
		 * ifshow: true/false msg: message  type: error/warning/success
		 */
		message(ifshow,msg,type) {
			this.$message({
				showClose: ifshow,
				message: msg,
				type: type
			})
		}
	},
	mounted() {
		this.getFormData1()
	}
}
</script>



