import Test from './views/test/Test.vue'
import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import UserList from './views/user/UserList.vue'
import PermiList from './views/permission/PermisList.vue'
import RoleList from './views/permission/RoleList.vue'
import DictionaryList from './views/dictionary/DictionaryList.vue'
import OperatingRecord from './views/system/OperatingRecord.vue'
import Statistics from './views/system/Statistics.vue'
import LoginLog from './views/system/LoginLog.vue'
import Ueditor from './views/ueditor.vue'
import InstituteList from './views/institute/InstituteList.vue'
import MajorList from './views/major/MajorList.vue'
import ClazzList from './views/clazz/ClazzList.vue'
import StudentList from './views/student/StudentList.vue'
import TeacherList from './views/teacher/TeacherList.vue'
import CourseList from './views/course/CourseList.vue'
import MyCourseList from './views/mycourse/MyCourseList.vue'
import CourseCenter from './views/coursecenter/CourseCenter.vue'
import GradeCenter from './views/grade/GradeCenter.vue'
import myExam from './views/exam/myExam.vue'
import examDetail from './views/exam/examDetail.vue'
import answer from './views/exam/answer.vue'
import paperManager from './views/exam/paperManager.vue'
import questionManager from './views/exam/questionManager.vue'

let router = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'icon iconfont icon-yonghurenxiang',//class
        children: [
            { path: '/main', component: Main, name: '', hidden: true },
            { path: '/user/list', component: UserList, name: '' },
            { path: '/ueditor', component: Ueditor, name: '' },
        ]
    },
	{
	    path: '/',
	    component: Home,
	    name: '',
	    children: [
	        { path: '/paper', component: paperManager, name: ''},
	        { path: '/question', component: questionManager, name: '' }
	    ]
	},
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'icon iconfont icon-Slice',
        children: [
            { path: '/perms/list', component: PermiList, name: '' },
            { path: '/role/list', component: RoleList, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'icon iconfont icon-dic-manager',
        children: [
            { path: '/dict/list', component: DictionaryList, name: '' },
        ]
    },
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/institute/list', component: InstituteList, name: '' },
	    ]
	},
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/major/list', component: MajorList, name: '' },
	    ]
	},	
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/clazz/list', component: ClazzList, name: '' },
	    ]
	},	
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/student/list', component: StudentList, name: '' },
	    ]
	},	
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/teacher/list', component: TeacherList, name: '' },
	    ]
	},		
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/course/list', component: CourseList, name: '' },
	    ]
	},		
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/course/mycourse', component: MyCourseList, name: '' },
			{ path: '/exam', component: myExam, name: '' },
			{ path: '/examDetail', component: examDetail, name: '' },
			{ path: '/answer', component: answer, name: '' }
	    ]
	},	
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/course/center', component: CourseCenter, name: '' },
	    ]
	},	
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'icon iconfont icon-dic-manager',
	    children: [
	        { path: '/grade/center', component: GradeCenter, name: '' },
	    ]
	},						
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'icon iconfont icon-xitongguanli',
        children: [
            { path: '/sys/operateRecord', component: OperatingRecord, name: '' },
            { path: '/sys/loginLog', component: LoginLog, name: '' },
            { path: '/sys/statistics', component: Statistics, name: '' },

        ]
    },
    {
        path: '/',
        component: Home,
        name: 'TEST',
        iconCls: 'icon iconfont icon-ceshi',
        children: [
            { path: '/Test', component: Test, name: '' },
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];


export default router;



