
<template>
	<el-row>
		<!---->
		<el-col :span="64" class="toolbar" style="padding-bottom: 0px;with:100%;height:100%">
			<el-form :inline="true" :model="filters" ref="filters">
				<el-form-item>
					<el-input placeholder="" v-model="filters.keyword1"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" class="el-icon-search" v-on:click="getFormData1"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="success" class="el-icon-plus" v-on:click="showDialogForm"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="danger" class="el-icon-delete" @click="delAttr"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<el-col>
			<el-table
			v-loading="listLoading" element-loading-text=""
      :data="tableData1"
			@selection-change="handleSelectionChange1">
			<el-table-column type="selection" width="55">
			</el-table-column>
<!--      <el-table-column
        prop="id"
        label="id">
      </el-table-column> -->
	  <el-table-column
	    prop="name"
	    label="" sortable>
	  </el-table-column>
      <el-table-column
        prop="courseTypeName"
        label="" sortable>
      </el-table-column>
	  <el-table-column
	    prop="credit"
	    label="" sortable>
	  </el-table-column>
	  <el-table-column
	    prop="teachersName"
	    label="" sortable>
	  </el-table-column>
	  <el-table-column
	    prop="studentNum"
	    label="" sortable>
	  </el-table-column>
<!-- 			<el-table-column
			      fixed="right"
			      label=""
						width="180px"
						align="center">
			      <template slot-scope="scope">
			      	<el-button
							  @click.native="selDetails(scope.row.id)"
			          type="text"
			          size="small">
			          
			        </el-button>
			      </template>
			  </el-table-column> -->
    </el-table>
		</el-col>

		<el-col>
			<div class="block" style="float: right;margin-right: 10px;margin-top: 10px;">
				<el-pagination
					@size-change="handleSizeChange1"
					@current-change="handleCurrentChange1"
					:current-page="startPage1"
					:page-sizes="pageSizes1"
					:page-size="pageSize1"
					layout="total, sizes, prev, pager, next, jumper"
					:total="total1">
				</el-pagination>
		 </div>
	</el-col>

<!--  -->
	<el-dialog title="" :visible.sync="dialogFormVisible1">
	<div style="width:60%;margin: 0 auto">
	<el-form ref="attr" :model="attr" :inline="false" label-width="90px" class="demo-ruleForm">
		<el-form-item label="" prop="courseType2" :rules="[{ required: true, message: '', trigger: 'change,blur' }]">
			<el-select v-model="attr.courseType2" filterable placeholder="">
				<el-option
					v-for="item in courseTypes"
					:key="item.dictCode"
					:label="item.dictValue"
					:value="item.dictCode">
				</el-option>
			</el-select>
		</el-form-item>		
		<el-form-item label="" prop="name2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
			<el-input  type="text" v-model="attr.name2" placeholder="" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item label="" prop="credit2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^([1-9]|10)$/, message: '1-10', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.credit2" placeholder="" auto-complete="off"></el-input>
		</el-form-item>	
		<el-form-item label="" prop="teacherIds" :rules="[{ required: true, message: '', trigger: 'change,blur' }]">
			<el-select v-model="attr.teacherIds" multiple filterable placeholder="">
				<el-option
					v-for="item in teachers"
					:key="item.id"
					:label="item.name"
					:value="item.id">
				</el-option>
			</el-select>
		</el-form-item>		
<!-- 		<el-form-item label="">
			<el-date-picker
					v-model="attr.signStime2"
					type="date"
					:picker-options="pickerBeginDateBefore"
					format="yyyy-MM-dd"
					placeholder="">
			</el-date-picker>
		</el-form-item>
		<el-form-item label="" label-width="25px">
			<el-date-picker
					v-model="attr.signEtime2"
					type="date"
					format="yyyy-MM-dd"
					:picker-options="pickerBeginDateAfter"
					placeholder="">
			</el-date-picker>
		</el-form-item>
		<el-form-item label="">
			<el-date-picker
					v-model="attr.examStime2"
					type="date"
					:picker-options="pickerBeginDateBefore2"
					format="yyyy-MM-dd"
					placeholder="">
			</el-date-picker>
		</el-form-item>
		<el-form-item label="" label-width="25px">
			<el-date-picker
					v-model="attr.examEtime2"
					type="date"
					format="yyyy-MM-dd"
					:picker-options="pickerBeginDateAfter2"
					placeholder="">
			</el-date-picker>
		</el-form-item>	 -->	

		<el-form-item label="" prop="signStime2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
			<el-input type="date" v-model="attr.signStime2"></el-input>
		</el-form-item>
		<el-form-item label="" prop="signEtime2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
			<el-input type="date" v-model="attr.signEtime2"> </el-input>
		</el-form-item>		
		<el-form-item label="" prop="examStime2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
			<el-input type="date" v-model="attr.examStime2"></el-input>
		</el-form-item>
		<el-form-item label="" prop="examEtime2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
			<el-input type="date" v-model="attr.examEtime2"></el-input>
		</el-form-item>			
	</el-form>
 </div>
	<div slot="footer" class="dialog-footer">
		<el-button @click="dialogFormVisible1 = false"> </el-button>
		<el-button @click="resetForm('attr')"></el-button>
		<el-button type="primary" @click="submitForm('attr')"> </el-button>
	</div>
</el-dialog>

	</el-row>


</template>

<script>
import http from '../../utils/http'

export default {
	data() {
		return {
			filters: {
				keyword1: ''
			},
			attr: {
				name2: '',
				courseType2: '',
				credit2: '',
				teacherIds:[],
				signStime2:'',
				signEtime2:'',
				examStime2:'',
				examEtime2:''
			},
			courseTypes:[],     //
			teachers:[],       //
			listLoading: false, // 
			pageSizes1: [30, 50, 80, 100],
			pageSizes2: [5, 10],
			startPage1: 1,
			startPage2: 1,
			pageSize1: 30,
			pageSize2: 5,
			total1: 0,
			total2: 0,
			tableData1: [],
			tableData2: [],
			total1: 0,
			total2: 0,
			page1: 1,
			page2: 1,
			dialogTableVisible1: false,
			dialogFormVisible1: false,
			dialogFormVisible2: false,
			innerVisible: false,
			attrIds: [], // ids
			detailIds: [], // ids
			attrId: '',
			pickerBeginDateBefore:{
				disabledDate: (time) => {
					let beginDateVal = this.attr.signEtime2;
					if (beginDateVal) {
						return time.getTime() > beginDateVal;
					}
				}
			},
			pickerBeginDateAfter:{
				disabledDate: (time) => {
					let beginDateVal = this.attr.signStime2;
					if (beginDateVal) {
						return time.getTime() < beginDateVal;
					}
				}
			},
			pickerBeginDateBefore2:{
				disabledDate: (time) => {
					let beginDateVal = this.attr.examEtime2;
					if (beginDateVal) {
						return time.getTime() > beginDateVal;
					}
				}
			},
			pickerBeginDateAfter2:{
				disabledDate: (time) => {
					let beginDateVal = this.attr.examStime2;
					if (beginDateVal) {
						return time.getTime() < beginDateVal;
					}
				}
			}		
		}
	},
	methods: {
		// 
		async getFormData1 () {
			let _this = this
			_this.listLoading = true
			let params = {
				startPage: _this.startPage1,
				pageSize: _this.pageSize1,
				name: _this.filters.keyword1
			}
			let data = await http.get('course/list', params)

			if(!data.data) {
				_this.listLoading = false
				return
			}

			if (data.data.status === 200) {
				 _this.tableData1 = data.data.data
				 _this.total1 = data.data.total
			} else {
				_this.message(true,data.data.msg,'error')
				_this.formData1 = []
			}
			_this.listLoading = false
		},
		// 
		showDialogForm() {
			this.dialogFormVisible1 = true
			this.getCourseType()
			this.getAllTeacher ()
		},
		// 
		async getCourseType () {
				let _this = this
				let param = {
					dictTypeCode:'COURSE_TYPE'
				}
				let data = await http.get('dict/findListByDictTypeCode',param)
				if(!data.data) {
					return
				}
				if (data.data.status === 200) {
				   _this.courseTypes = data.data.data
				 } else {
				  _this.message(true,data.data.msg,'error')
				  _this.courseTypes = []
				}
			},
		// 
		async getAllTeacher () {
				let _this = this
				let data = await http.get('teacher/findAllTeacher')
				if(!data.data) {
					return
				}
				if (data.data.status === 200) {
				   _this.teachers = data.data.data
				 } else {
				  _this.message(true,data.data.msg,'error')
				  _this.teachers = []
				}
			},
						
		// 
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addCourse()
				} else {
					console.log('error submit!!');
					return false
				}
			})
		},
		// 
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		// 
		async addCourse() {
			let _this = this
			let stime = _this.attr.signStime2
			let etime = _this.attr.signEtime2
			if(stime>etime){
				 _this.message(true,'','error')
				 return
			}
			stime = _this.attr.examStime2
			etime =  _this.attr.examEtime2
			if(stime>etime){
				 _this.message(true,'','error')
				 return
			}			
			let params = {
				name: _this.attr.name2,
				courseType: _this.attr.courseType2,
				credit: _this.attr.credit2,
				teacherIds: _this.attr.teacherIds,
				signStime: _this.attr.signStime2,
				signEtime: _this.attr.signEtime2,
				examStime: _this.attr.examStime2,
				examEtime: _this.attr.examEtime2
			}
			let data = await http.post("course/add", params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				  _this.resetForm('attr')
				  _this.dialogFormVisible1=false
          _this.message(true,data.data.msg,'success')
					_this.getFormData1()
			} else {
         _this.message(true,data.data.msg,'error')
			}
		},
		// 
		async delAttr () {
			if (this.attrIds.length === 0) {
				 this.message(true,'','warning')
				 return
			}
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {
				this.delAttrs()
			}).catch(() => {
				this.message(true,'','warning')
			})
		},
		// 
		async delAttrs() {
      let _this = this
			let params = {
				ids: _this.attrIds
			}
			let data =await http.post('course/delete', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.message(true,data.data.msg,'success')
			} else {
				 _this.message(true,data.data.msg,'error')
			}
			_this.getFormData1()
		},
		// 
		handleSelectionChange1(val) {
			this.attrIds = []
			if (val) {
			 val.forEach(row => {
				 this.attrIds.push(row.id)
			 });
		 }
		},
		// 
		handleSizeChange1 (val) {
			this.pageSize1 = val
			this.getFormData1()
		},
		// 
		handleCurrentChange1 (val) {
			this.startPage1 = val
			this.getFormData1()
		},

		// 
		submitForm1(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addDetail()
				} else {
					console.log('error submit!!');
					return false
				}
			})
		},
		// 
		async addDetail() {
			 let _this = this
			 let params = {
				 id: _this.attrId,
				 name: _this.detail.name
			 }
			 let data = await http.post('SysApi/v1/addAttributeDetail', params)

			 if(!data.data) {
			 	return
			 }

			 if (data.data.status === 200) {
 				  _this.resetForm('detail')
           _this.message(true,data.data.msg,'success')
 					_this.selDetails(this.attrId)
 			} else {
          _this.message(true,data.data.msg,'error')
 			}
		},
		// 
		async selDetails(id) {
			let _this = this
			_this.dialogTableVisible1 = true
			_this.attrId = id
			let params = {
				id: id,
				pageSize: _this.pageSize2,
				startPage: _this.startPage2
			}
			let data = await http.get('SysApi/v1/findAttributesDetailByPage', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.tableData2 = data.data.data
				 _this.total2 = data.data.total
			} else {
				_this.message(true,data.data.msg,'error')
				_this.formData2 = []
			}
		},
		// 
		async delDetails() {
      let _this = this
			if (_this.detailIds.length === 0) {
				 this.message(true,'','warning')
				 return
			}

			let params = {
				ids: _this.detailIds
			}
			let data =await http.post('SysApi/v1/delAttributeDetails', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.message(true,data.data.msg,'success')
			} else {
				 _this.message(true,data.data.msg,'error')
			}
			_this.selDetails(this.attrId)
		},
		// 
		handleSelectionChange2(val) {
			this.detailIds = []
			if (val) {
			 val.forEach(row => {
				 this.detailIds.push(row.id)
			 });
		 }
		},
		// 
		handleSizeChange2 (val) {
			this.pageSize2 = val
			this.selDetails(this.attrId)
		},
		// 
		handleCurrentChange2 (val) {
			this.startPage2 = val
			this.selDetails(this.attrId)
		},
		/**
		 * ifshow: true/false msg: message  type: error/warning/success
		 */
		message(ifshow,msg,type) {
			this.$message({
				showClose: ifshow,
				message: msg,
				type: type
			})
		}
	},
	mounted() {
		this.getFormData1()
	}
}
</script>



