import babelpolyfill from 'babel-polyfill' //  vuex requires a Promise polyfill in this browser 
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css' //    import 'element-ui/lib/theme-default/index.css'
import VueRouter from 'vue-router'
import axios from 'axios' // Promise  nodejs  HTTP 
import store from './vuex/store'
import Vuex from 'vuex'
import './common/icon/iconfont.css' // 
import routes from './routes' // 
import NProgress from 'nprogress' // 
import 'nprogress/nprogress.css' // 
import Moment from 'moment'

Vue.use(Moment)
Vue.use(ElementUI)
Vue.use(VueRouter)
Vue.use(Vuex)

// cookie
axios.defaults.withCredentials = true

Vue.config.productionTip = false

Vue.prototype.imgService = 'ftp://192.168.239.1' // url
Vue.prototype.uploadService = 'http://localhost:8089/' //url

// 
Vue.filter('dataFormat', function (inputstr, showsplit, showweek) {
     inputstr = inputstr + ""; //
    var date = "";
    var month = new Array();
    var week = new Array();

    month["Jan"] = 1; month["Feb"] = 2; month["Mar"] = 3; month["Apr"] = 4; month["May"] = 5; month["Jun"] = 6;
    month["Jul"] = 7; month["Aug"] = 8; month["Sep"] = 9; month["Oct"] = 10; month["Nov"] = 11; month["Dec"] = 12;
    week["Mon"] = ""; week["Tue"] = ""; week["Wed"] = ""; week["Thu"] = ""; week["Fri"] = ""; week["Sat"] = ""; week["Sun"] = "";

    var str = inputstr.split(" ");

    date = str[5];
    date += showsplit + month[str[1]] + showsplit + str[2] + " " + str[3];
    if(showweek){
    date += " " + " " + week[str[0]];
    }

    return date;
})
//moment
Vue.filter('converTime',function(data,formatStr){  
    return Moment(data).format(formatStr);
})
// 
// var a="Wed Mar 22 13:38:37 CST 2017";
//    var b=new Date(a);
//    var c=todate(a, "-", true);
//    var d=todate(a, "", false);
//
//    alert(b); //Thu Mar 23 2017 03:38:37 GMT+0800 ()
//    alert(c); //2017-3-22 
//    alert(d); //2017322

const router = new VueRouter({
  routes
})

// url 
router.beforeEach((to, from, next) => {
  if (to.path == '/login') {
    sessionStorage.removeItem('user');
  }
  let user = JSON.parse(sessionStorage.getItem('user'));
  if (!user && to.path != '/login') {
    next({ path: '/login' })
  } else if(user && to.path != '/login' && to.path != '/404' && to.path != '/'){
    let permis = user.userPerms
    let allow = true
    for (var i = 0,length = permis.length; i < length; i++) {
      let children = permis[i].children
      for (var j = 0,leng = children.length; j < leng; j++) {
        if(to.path === children[j].url) {
          allow = true
        }
      }
    }
    if (allow) {
      console.log('' + to.path);
      next()
    } else {
      console.log('' + to.path);
      next({ path: '/404' })
    }
  } else {
    next()
  }
})
router.afterEach(transition => {
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')



